import React, { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import { ChevronRight, ChevronLeft, Eye, EyeOff, ZoomIn, ZoomOut, Rotate3d } from 'lucide-react';

const AnatomicalShoulder = () => {
  const [currentStep, setCurrentStep] = useState(0);
  const [visibleLayers, setVisibleLayers] = useState({
    skin: true,
    muscle: true,
    bone: true,
    implant: false,
    surgical: false
  });
  const [viewAngle, setViewAngle] = useState(0);
  const [zoom, setZoom] = useState(1);

  // Detailed anatomical structure paths
  const anatomyPaths = {
    // Skin and surface anatomy
    skinOutline: `M150,50 C200,30 250,30 300,50 C350,70 380,100 390,150 
                  C400,200 390,250 360,290 C330,330 290,350 250,360 
                  C210,370 170,360 140,330 C110,300 90,260 85,210 
                  C80,160 100,100 150,50`,
    
    // Deltoid muscle
    deltoid: `M160,70 C200,50 240,50 280,70 C320,90 345,120 355,160 
              C365,200 355,240 330,270 C305,300 270,315 235,320 
              C200,325 165,315 140,290 C115,265 100,230 95,190 
              C90,150 110,100 160,70`,
              
    // Rotator cuff complex
    rotatorCuff: `M180,90 C210,75 240,75 270,90 C300,105 320,130 330,160 
                  C340,190 335,220 315,245 C295,270 270,285 240,290 
                  C210,295 180,285 160,265 C140,245 130,215 125,185 
                  C120,155 150,105 180,90`,
    
    // Scapula
    scapula: `M200,100 Q230,90 260,100 Q290,110 310,130 
              Q330,150 340,180 L320,190 Q310,160 290,140 
              Q270,120 240,110 Q210,100 200,100`,
    
    // Humerus with detailed head
    humerus: `M210,130 Q240,120 260,140 Q280,160 290,190 
              L270,200 Q260,170 240,150 Q220,130 210,130
              M240,190 L240,350`,
              
    // Glenoid cavity with labrum
    glenoid: `M220,140 Q240,135 255,145 Q270,155 280,175 
              Q290,195 285,215 L265,210 Q270,190 260,175 
              Q250,160 235,155 Q220,150 220,140`,
              
    // Surgical implant components
    implantStem: `M235,160 L235,340 M220,160 L250,160`,
    implantHead: `M210,140 Q235,130 260,140 Q285,150 295,170 
                  Q305,190 300,210 L280,205 Q285,185 275,170 
                  Q265,155 240,150 Q215,145 210,140`,
    
    // Surgical approach lines
    surgicalIncision: `M180,80 L320,200`
  };

  // Surgical procedure steps from Dr. Jobin's documentation
  const surgerySteps = [
    {
      id: 0,
      title: "Initial Anatomy",
      description: "Normal shoulder anatomy before surgical intervention",
      visibleLayers: ['skin', 'muscle', 'bone'],
      highlight: null
    },
    {
      id: 1,
      title: "Surgical Approach",
      description: "5-6 inch incision with careful dissection through deltoid",
      visibleLayers: ['skin', 'muscle', 'bone', 'surgical'],
      highlight: 'surgicalIncision'
    },
    {
      id: 2,
      title: "Humeral Preparation",
      description: "Removal of arthritic humeral head and preparation of shaft",
      visibleLayers: ['muscle', 'bone'],
      highlight: 'humerus'
    },
    {
      id: 3,
      title: "Glenoid Preparation",
      description: "Socket preparation for implant placement",
      visibleLayers: ['bone'],
      highlight: 'glenoid'
    },
    {
      id: 4,
      title: "Implant Placement",
      description: "Installation of prosthetic components with or without cement",
      visibleLayers: ['bone', 'implant'],
      highlight: ['implantStem', 'implantHead']
    },
    {
      id: 5,
      title: "Final Result",
      description: "Completed shoulder replacement with all layers visible",
      visibleLayers: ['skin', 'muscle', 'bone', 'implant'],
      highlight: null
    }
  ];

  // Anatomical layer definitions with colors and properties
  const anatomicalLayers = {
    skin: {
      color: "#ffdbac",
      opacity: 0.9,
      paths: [anatomyPaths.skinOutline]
    },
    muscle: {
      color: "#ff6b6b",
      opacity: 0.8,
      paths: [anatomyPaths.deltoid, anatomyPaths.rotatorCuff]
    },
    bone: {
      color: "#f0e6d2",
      opacity: 1,
      paths: [anatomyPaths.scapula, anatomyPaths.humerus, anatomyPaths.glenoid]
    },
    implant: {
      color: "#c0c0c0",
      opacity: 1,
      paths: [anatomyPaths.implantStem, anatomyPaths.implantHead]
    },
    surgical: {
      color: "#ff0000",
      opacity: 0.5,
      paths: [anatomyPaths.surgicalIncision]
    }
  };

  const handleStepChange = (direction) => {
    const newStep = currentStep + direction;
    if (newStep >= 0 && newStep < surgerySteps.length) {
      setCurrentStep(newStep);
      setVisibleLayers(prevLayers => ({
        ...prevLayers,
        ...surgerySteps[newStep].visibleLayers.reduce((acc, layer) => ({
          ...acc,
          [layer]: true
        }), {})
      }));
    }
  };

  return (
    <div className="w-full max-w-6xl mx-auto p-6 bg-white rounded-xl shadow-lg">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <h2 className="text-2xl font-bold text-gray-900">
            Total Shoulder Arthroplasty Procedure
          </h2>
          <p className="text-gray-600">
            {surgerySteps[currentStep].description}
          </p>
        </div>
        
        {/* View Controls */}
        <div className="flex gap-3">
          <button 
            onClick={() => setZoom(prev => Math.min(prev + 0.1, 2))}
            className="p-2 rounded-lg hover:bg-gray-100"
          >
            <ZoomIn />
          </button>
          <button 
            onClick={() => setZoom(prev => Math.max(prev - 0.1, 0.5))}
            className="p-2 rounded-lg hover:bg-gray-100"
          >
            <ZoomOut />
          </button>
          <button 
            onClick={() => setViewAngle(prev => (prev + 45) % 360)}
            className="p-2 rounded-lg hover:bg-gray-100"
          >
            <Rotate3d />
          </button>
        </div>
      </div>

      {/* Main Visualization */}
      <div className="relative aspect-video bg-gray-50 rounded-lg overflow-hidden">
        <motion.svg
          viewBox="0 0 500 400"
          className="w-full h-full"
          style={{
            transform: `scale(${zoom}) rotate(${viewAngle}deg)`,
            transformOrigin: 'center',
          }}
        >
          {/* Render each anatomical layer based on visibility */}
          {Object.entries(anatomicalLayers).map(([layerName, layer]) => (
            visibleLayers[layerName] && layer.paths.map((path, index) => (
              <motion.path
                key={`${layerName}-${index}`}
                d={path}
                fill={layer.color}
                opacity={layer.opacity}
                stroke="#333"
                strokeWidth="1"
                initial={{ opacity: 0 }}
                animate={{ opacity: layer.opacity }}
                transition={{ duration: 0.5 }}
                className={surgerySteps[currentStep].highlight === layerName ? 'stroke-2 stroke-blue-500' : ''}
              />
            ))
          ))}

          {/* Labels and annotations */}
          <g className="text-sm">
            {visibleLayers.bone && (
              <>
                <text x="240" y="130" className="fill-gray-700">Glenoid</text>
                <text x="270" y="200" className="fill-gray-700">Humerus</text>
              </>
            )}
            {visibleLayers.muscle && (
              <text x="180" y="150" className="fill-gray-700">Deltoid Muscle</text>
            )}
          </g>
        </motion.svg>
      </div>

      {/* Navigation Controls */}
      <div className="mt-6 flex justify-between items-center">
        <button
          onClick={() => handleStepChange(-1)}
          disabled={currentStep === 0}
          className="px-4 py-2 flex items-center gap-2 text-blue-600 disabled:text-gray-400"
        >
          <ChevronLeft /> Previous Step
        </button>

        {/* Step Indicators */}
        <div className="flex gap-2">
          {surgerySteps.map((step, index) => (
            <div
              key={step.id}
              className={`w-2 h-2 rounded-full ${
                index === currentStep ? 'bg-blue-600' : 'bg-gray-300'
              }`}
            />
          ))}
        </div>

        <button
          onClick={() => handleStepChange(1)}
          disabled={currentStep === surgerySteps.length - 1}
          className="px-4 py-2 flex items-center gap-2 text-blue-600 disabled:text-gray-400"
        >
          Next Step <ChevronRight />
        </button>
      </div>

      {/* Layer Controls */}
      <div className="mt-6 grid grid-cols-5 gap-4">
        {Object.keys(visibleLayers).map(layer => (
          <button
            key={layer}
            onClick={() => setVisibleLayers(prev => ({...prev, [layer]: !prev[layer]}))}
            className={`flex items-center justify-between p-2 rounded-lg ${
              visibleLayers[layer] ? 'bg-blue-50 text-blue-600' : 'bg-gray-100 text-gray-600'
            }`}
          >
            <span className="capitalize">{layer}</span>
            {visibleLayers[layer] ? <Eye size={16} /> : <EyeOff size={16} />}
          </button>
        ))}
      </div>
    </div>
  );
};

export default AnatomicalShoulder;